import React from "react"

import "../styles/components/animatedText.scss"


export default class AnimatedText extends React.Component {

    constructor(props) {
        super(props)
        this.shouldRotate = (props.shouldRotate) ? " shouldRotate" : "";
        this.animateOnLoad = (props.animateOnLoad) ? " animateOnLoad" : "";
        // récupère les potentielles class custom et les appliques
        this.customCssClass = " " + props.className
        // récupère un potentiel objet style passé en attribut et l'applique
        this.customStyle = props.style

        this.children = props.children

    }

    render() {
        return (
            <div style={this.customStyle} className={"animatedText" + this.shouldRotate + this.animateOnLoad + this.customCssClass + ((this.props.isHovered) ? " animated" :  "")}>
                <span></span>
                <span>{(this.props.children) ? this.props.children : this.props.text }</span>
            </div>
        )
    }
}
